<template>
  <div>
    <h3>food</h3>
    <go-back></go-back>
<ul>
  <li class="box" v-for='item in foods' :key="item.foodId" @click="toDetail(item.foodId)">
<h3>{{item.name}}</h3>
<p>价格：{{item.price|filterPrice}}</p>
  </li>
</ul>
  </div>
</template>

<script>
import {reqFood} from "../../utils/http"
export default {
data() {
  return {
    foods:[]
  }
},
mounted(){
    reqFood().then(res=>{
      this.foods=res.data.data

    })
  },
methods: {
  toDetail(foodId){
    this.$router.push('/foodDetail/'+foodId)
  }
},
}
</script>

<style>

</style>



